<template>
  <div class="login-container" :class="{
    'anta': cooperationName === 'anta',
    'page-feedback': $route.name === 'Feedback',
    }" :style="{
      backgroundImage: backgroundUrl
    }">
    <div class="login-index-wrapper no-collapse">
      <router-view class="login-index" />

      <!-- 页脚 -->
      <footer class="login-footer-section clearfix">
        <login-footer class="login-footer" />
      </footer>
    </div>
  </div>
</template>

<script>
import envs from '@/utils/envs';
import axios from '@/utils/axios';
import SystemUnderMaintenance from '@/views/components/SystemUnderMaintenance.vue';
import LoginFooter from './components/LoginFooter';

async function showMaintenance(maintenance) {
  const end = maintenance.end ?? maintenance.to;
  const maintenanceStorage = window.localStorage.getItem('maintenanceReaded') ? +window.localStorage.getItem('maintenanceReaded') : 0;
  if (maintenanceStorage < maintenance.start && Date.now() < end) {
    await this.$injectDialog({
      is: SystemUnderMaintenance,
      props: maintenance,
    }, {
      group: 'unclosable',
    });
    window.localStorage.setItem('maintenanceReaded', `${maintenance.start}`);
  }
}
export default {
  data() {
    return {
      url: '',
    };
  },
  components: {
    LoginFooter,
  },
  async mounted() {
    this.url = await this.getBackgroundUrl();
    const maintenance = null;
    // const maintenance = {
    //   start: 1603969383035,
    //   from: 1604106000000,
    //   to: 1604159999999,
    //   descs: {
    //     cn: '尊敬的用户：\n为了全面提升本平台的使用效果，我们将在10月31日（本周六）进行资源升级，维护期间会暂时关闭平台的使用，维护时间约持续一天，很抱歉可能给您带来的不便，感谢您的理解与支持。',
    //     en: 'Scheduled Maintenance & Upgrade in Progress for 24 hours from midnight '
    //       + '00:00 to 23:59 on Saturday Oct 31, 2020 (Beijing Time) or from Friday, '
    //       + 'Oct 30, 2020 9:00am to 8:59am next day on Saturday, Oct 31, '
    //       + '2020 (Los Angeles Time) We’ve got some exciting new things in store for you. '
    //       + 'Please check back soon.',
    //   },
    // };
    if (maintenance) {
      this.$act(this.showMaintenance, [maintenance]);
    }
  },
  methods: {
    showMaintenance,
    isValidImg(url) {
      return url && this.cooperationName.includes('anta');
    },
    async getBackgroundUrl() {
      let res;
      if (this.cooperationName.includes('anta')) {
        res = (await axios({
          method: 'GET',
          url: '/anta-material/system/background/get',
          params: {
            businessType: this.cooperationName.includes('anta') ? 1 : 0,
          },
        })).data;
      }
      return this.isValidImg(res) ? `url(${res})`
        : `url(${require('@/assets/img/login/login-bg-anta.png')})`;
    },
  },
  computed: {
    /**
     * @return {string}
     */
    cooperationName() {
      return envs.VUE_APP_COOPERATION;
    },
    /**
     * @return {string}
     */
    backgroundUrl() {
      // sdtc-public-picture.oss-cn-shenzhen.aliyuncs.com/anta/staging/anta-background
      if (this.cooperationName.includes('anta')) {
        return this.url;
      }
      return `url(${require('@/assets/img/login/login-bg.png')}), linear-gradient(-45deg, #1a7bdb, #7fd9f9)`;
    },
  },
};
</script>

