
























/* eslint-disable indent */
import { computed, defineComponent } from '@vue/composition-api';
import date from '@/filters/date';
import { i18n } from '@/i18n';
// 众所周知常用的程序语言支持的时区属于中国的有六个 ...
/*
  Asia/Chongqing
  Asia/Shanghai
  Asia/Urumqi
  Asia/Macao
  Asia/Hong_Kong
  Asia/Taipei
 */
const timezone2zone = {
  'Asia/Shanghai': {
    cn: '北京',
    en: 'Beijing',
  },
} as { [key: string]: { [key: string]: string } };
export default defineComponent({
  dialogify: {
    props: {
      width: '600px',
    },
  },
  props: {
    from: {
      type: Number,
      default: () => {
        return Date.now();
      },
    },
    to: {
      type: Number,
      default: () => {
        return Date.now();
      },
    },
    timezone: {
      type: String,
      default: 'Asia/Shanghai',
    },
    descs: {
      type: Object as () => {
        [lang: string]: string
      },
    },
  },
  setup(props) {
    const zone = computed(() => {
      return timezone2zone[props.timezone][i18n.language];
    });
    const desc = computed(() => {
      return props.descs?.[i18n.language] ?? '';
    });
    const lang = computed(() => i18n.language);

    const startTime = computed(() => {
      const beijingTimeStr = new Date(props.from).toLocaleString('en-US', { timeZone: props.timezone });
      const d = new Date(beijingTimeStr);
      return date(d, 'YYYY-MM-DD HH:mm');
    });
    const endTime = computed(() => {
      const beijingTimeStr = new Date(props.to).toLocaleString('en-US', { timeZone: props.timezone });
      const d = new Date(beijingTimeStr);
      return date(d, 'YYYY-MM-DD HH:mm');
    });
    return {
      lang,
      desc,
      zone,
      startTime,
      endTime,
    };
  },
});
